/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Inter", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
  color: #000000;
}

p {
  text-align: justify;
  font-size: 1.7rem;
  font-weight: 400;
}

.contenedor {
  width: 90%;
  max-width: 135rem;
  margin: 0 auto;
}

.contenedor-interior {
  width: 80%;
  max-width: 135rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

button {
  cursor: pointer;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Inter", sans-serif;
  margin: 2.5rem 0;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-family: "Inter", sans-serif;
  font-size: 3rem;
  font-weight: 800;
  text-align: center;
}

h3 {
  font-size: 3rem;
  margin: 0;
}

h4 {
  font-size: 2.6rem;
}

.contenido-centrado {
  display: flex;
  justify-content: center;
  align-items: center;
}

.titulo-h1-pagina {
  font-size: 5rem;
  font-weight: 800;
  color: #231F57;
  text-transform: uppercase;
  text-align: center;
  line-height: 1.1;
  margin-top: 4rem;
}

.alineacion-izquierda {
  text-align: left;
}

.contenedor-boton-atras {
  display: flex;
  align-items: center;
  margin-top: 3rem;
}

.boton-atras {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  color: #231F57;
  font-weight: 900;
}

label {
  font-family: "Poppins", sans-serif;
  font-size: 1.7rem;
  font-weight: 500;
  display: block;
}

.campos {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 5rem;
  margin-top: 4rem;
}

.inputs {
  width: 100%;
  padding: 0.5rem 0.5rem 0.5rem 0;
  border: none; /* Primero, eliminamos todos los bordes */
  border-bottom: 0.2rem solid #231F57; /* Luego, agregamos un borde solo en la parte inferior */
  outline: none;
}

textarea {
  width: 100%;
  height: 8rem;
  padding: 0.5rem 0.5rem 0.5rem 0;
  border: none; /* Primero, eliminamos todos los bordes */
  border-bottom: 0.2rem solid #231F57; /* Luego, agregamos un borde solo en la parte inferior */
  outline: none;
  resize: none;
}

.boton-formulario-azul {
  padding: 1.5rem 4rem;
  background-color: #231F57;
  border: none;
  color: #FFFFFF;
  border-radius: 0.5rem;
  font-family: "Poppins", sans-serif;
  cursor: pointer;
  float: right;
}

.boton-formulario-blanco {
  padding: 0.8rem;
  background-color: #FFFFFF;
  border: 0.1rem solid #231F57;
  color: #000000;
  border-radius: 0.5rem;
  font-family: "Poppins", sans-serif;
  cursor: pointer;
  text-align: center;
}

.card {
  background-color: #30BDBC;
  text-align: center;
  font-family: "Poppins", sans-serif;
  text-transform: uppercase;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 1.5rem;
  font-weight: 800;
  color: #231F57;
  font-size: 2.5rem;
  padding: 1.5rem 0;
  cursor: pointer;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.boton-blanco, .navegacion .boton-blanco {
  background-color: #FFFFFF;
  color: #231F57;
  font-weight: 600;
  padding: 1rem 2.5rem;
  text-align: center;
  margin-top: separacion/2;
  border-radius: 5rem;
  border: 0.1rem solid #231F57;
  font-size: 1.5rem;
}

.boton-azul, .boton-azul, .navegacion .boton-azul {
  background-color: #231F57;
  color: #FFFFFF;
  font-weight: 600;
  padding: 1rem 2.5rem;
  text-align: center;
  margin-top: separacion/2;
  border-radius: 5rem;
  border: 0.1rem solid #231F57;
  font-size: 1.5rem;
}

.boton-chatbot {
  background-color: #FFFFFF;
  color: #000000;
  font-weight: 600;
  padding: 1rem 2.5rem;
  text-align: center;
  margin-top: separacion/2;
  border-radius: 5rem;
  border: 0.1rem solid #231F57;
  font-size: 1.5rem;
}

.boton-rojo, .navegacion .boton-rojo {
  background-color: #FF3333;
  color: #FFFFFF;
  font-weight: 600;
  padding: 1rem 2.5rem;
  text-align: center;
  margin-top: separacion/2;
  border-radius: 5rem;
  border: 0.1rem solid #FF3333;
  font-size: 1.5rem;
}

.boton-perfil {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 0.3rem;
}

.footer {
  margin-top: 5rem;
  width: 98%;
  max-width: 140rem;
  margin-right: auto;
  margin-left: auto;
}
.footer p {
  font-weight: 400;
  font-size: 1.4rem;
  text-align: center;
}

.contenedor-footer {
  width: 100%;
  background-color: #231F57;
  display: grid;
  grid-template-columns: 1.3fr 1fr 1.5fr 1fr;
  column-gap: 2rem;
  border-radius: 3rem;
  margin-bottom: 1rem;
}
.contenedor-footer h6 {
  font-family: "Inter", sans-serif;
  color: #30BDBC;
  text-transform: uppercase;
  font-size: 1.4rem;
  font-weight: 900;
  margin-bottom: 2.5rem;
}
.contenedor-footer .navegacion-footer {
  display: flex;
  flex-direction: column;
  margin-bottom: 3rem;
}
.contenedor-footer .navegacion-footer a {
  color: #FFFFFF;
  font-size: 1.4rem;
  font-weight: 500;
  margin-top: 1.3rem;
}
.contenedor-footer .navegacion-footer a:first-child {
  margin-top: 0;
}
.contenedor-footer ul {
  list-style: none;
  display: flex;
  flex-direction: column;
  padding-left: 0;
  margin-top: 0;
}
.contenedor-footer li {
  color: #FFFFFF;
  font-size: 1.4rem;
  font-weight: 500;
  margin-top: 1.3rem;
  display: flex;
  gap: 1rem;
}
.contenedor-footer li:first-child {
  margin-top: 0;
}
.contenedor-footer .footer-1 {
  background-color: #FFFFFF;
  margin: 2.5rem 12rem 2.5rem 3rem;
  border-radius: 3rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 1rem;
}
.contenedor-footer .footer-1 h5 {
  font-family: "Inter", sans-serif;
  color: #30BDBC;
  text-transform: uppercase;
  font-size: 2rem;
  font-weight: 900;
  margin-bottom: 1rem;
  text-align: center;
  margin-top: 0.2rem;
  line-height: 2.5rem;
  display: flex;
  justify-content: center;
  text-align: center;
  flex-direction: column;
}
.contenedor-footer .footer-1 img {
  width: 80%;
}
.contenedor-footer .footer-2, .contenedor-footer .footer-3, .contenedor-footer .footer-4 {
  padding-left: 3rem;
}

.contenido-eslogan {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 4rem;
}
.contenido-eslogan h1 {
  color: #231F57;
  font-weight: 800;
  text-transform: uppercase;
  font-size: 5.9rem;
  text-align: left;
  line-height: 1.1;
  max-width: 70rem;
  margin-left: 2rem;
}
.contenido-eslogan .eslogan {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  background-image: url("../../build/img/Vector1.svg");
  background-size: contain; /* Ajusta la imagen para cubrir todo el cuadrado */
  background-repeat: no-repeat;
  background-position: center;
  background-position-y: 45%; /* Mover un poco hacia arriba */
  background-position-x: 0%;
  background-size: 80%;
}
.contenido-eslogan .eslogan_img {
  padding: 1rem 0 1rem 4rem;
}
.contenido-eslogan .eslogan_img img {
  height: 55rem;
  border-radius: 4rem;
}

.barra {
  display: flex;
  justify-content: space-between;
  padding-top: 1rem;
}
.barra .lado-izq {
  display: flex;
}
.barra .lado-izq img {
  height: 6rem;
  width: 6rem;
}
.barra .lado-izq .titulo {
  max-width: 20rem;
  color: #231F57;
  text-decoration: none;
  line-height: 1.2;
  font-weight: 800;
  font-family: "Poppins", sans-serif;
  font-size: 1.8rem;
  display: flex;
  justify-content: center;
  flex-direction: column;
  margin-left: 0.8rem;
}
.barra .lado-izq .titulo .segunda_parte {
  color: #30BDBC;
}

.navegacion {
  display: flex;
  align-items: center;
}
.navegacion a {
  color: #000000;
  font-size: 1.5rem;
  margin-right: 2rem;
  font-weight: 600;
}
.navegacion a:last-child {
  margin-right: 0;
}
.navegacion a.activo {
  color: #231F57;
  font-weight: 900;
}

.seccion-principal h2 {
  font-size: 3rem;
  font-weight: 800;
  text-align: center;
}
.seccion-principal .seccion {
  display: grid;
  grid-template-columns: 0.7fr 1fr;
  gap: 2rem;
}
.seccion-principal .seccion span {
  margin-top: 4rem;
  font-weight: 800;
}
.seccion-principal .seccion_img {
  display: flex;
  justify-content: center;
  align-items: center;
}
.seccion-principal .seccion_img img {
  width: 60%;
  border-radius: 100%;
}

.testimoniales-contenedor-cards {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2.4rem;
}
.testimoniales-contenedor-cards .testimoniales-card {
  background-color: #30BDBC;
  padding: 4rem;
  border-radius: 2.4rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.testimoniales-contenedor-cards .testimoniales-card img {
  width: 5.6rem;
  height: 5.6rem;
}
.testimoniales-contenedor-cards .testimoniales-card strong {
  text-transform: uppercase;
}

.estadisticas {
  margin-top: 5rem;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 2rem;
}
.estadisticas .estadisticas-img {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 12rem;
}
.estadisticas .estadisticas-img img {
  border-radius: 4rem;
}
.estadisticas .estadisticas-informacion {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 1fr);
  grid-column-gap: 5rem;
  padding: 5rem;
}
.estadisticas .estadisticas-informacion h3 {
  font-weight: 900;
  font-size: 3.5rem;
  color: #231F57;
}
.estadisticas .estadisticas-informacion p {
  font-weight: 500;
  margin: 0;
}

.contenedor-chatbot {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 2rem;
}
.contenedor-chatbot .boton-abrir {
  color: #000000;
  font-size: 1.5rem;
  font-weight: 700;
  padding: 1.5rem;
  box-shadow: 2px 2px 2px 3px rgba(0, 0, 0, 0.2);
}
.contenedor-chatbot .boton-abrir:hover {
  background-color: #DF662B;
  border-color: #DF662B;
  color: #FFFFFF;
  transition: all 0.5s;
}

.chat-container {
  width: 500px;
  border: 0.25rem solid #231F57;
  border-radius: 2rem;
  box-shadow: 0 0 1rem rgba(0, 0, 0, 0.1);
  background-color: #FFFFFF;
  overflow: hidden;
  display: none;
  position: fixed;
  bottom: 7rem;
  left: 40rem;
  z-index: 1000;
}

.chat-header {
  background-color: #231F57;
  font-family: "Poppins", sans-serif;
  font-weight: 400;
  color: #FFFFFF;
  padding: 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.chat-header-izq {
  display: flex;
  gap: 1rem;
}

.chat-title {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}

.chat-title span {
  line-height: 1.2;
}

.chat-status {
  color: #4CAF50;
}

.close-button {
  background-color: #DF662B;
  color: #FFFFFF;
  border: none;
  border-radius: 0.5rem;
  padding: 0.5rem 1rem;
  font-family: "Poppins", sans-serif;
  font-weight: 600;
}
.close-button:hover {
  background-color: #FFFFFF;
  color: #DF662B;
  transition: all 0.5s;
}

.chat-box {
  height: 200px;
  padding: 20px 10px;
  box-sizing: border-box;
  overflow-y: scroll;
}
.chat-box .men {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.response {
  display: flex;
  justify-content: flex-end;
  align-items: flex-start;
  gap: 0.5rem;
  margin-top: 0.2rem;
  margin-bottom: 1rem;
}

.response-bot {
  display: flex;
  justify-content: flex-start;
  gap: 0.5rem;
  margin-bottom: 10px;
}

.message {
  padding: 5px 10px;
  border-radius: 10px;
  max-width: 80%;
  display: flex;
  align-items: flex-start;
}

.bot-message {
  background-color: #E3E3E3;
}

.user-message {
  background-color: #DF662B;
  color: white;
  align-self: flex-end;
}

.question-buttons {
  display: flex;
  flex-direction: column;
  padding: 10px;
  border-top: 1px solid #ccc;
  background-color: #fff;
  height: 200px;
  box-sizing: border-box;
  overflow-y: scroll;
}

.question-buttons button {
  margin: 5px 0;
  padding: 10px;
  border: none;
  border-radius: 5px;
  background-color: #E3E3E3;
  color: black;
  cursor: pointer;
  text-align: left;
}
.question-buttons button:hover {
  background-color: #DF662B;
  color: #FFFFFF;
  transition: all 0.5s;
}

.titulo-h3-pagina {
  margin-top: -4.5rem;
  font-family: "Poppins", sans-serif;
  color: #231F57;
  text-transform: uppercase;
  font-weight: 800;
}

.contenedor-table {
  background-color: #231F57;
}

.search-container {
  position: relative;
  width: 50%; /* Ajusta el ancho según sea necesario */
}

#search {
  width: 100%;
  padding: 10px 40px 10px 40px;
  border: 1px solid #EAECF0;
  border-radius: 0.7rem;
  margin: 1rem;
  color: #000000;
}
#search::placeholder {
  color: #000000;
}
#search:focus {
  outline: none;
}

.search-icon {
  position: absolute;
  top: 50%;
  left: 20px; /* Ajusta la distancia desde el borde derecho */
  transform: translateY(-50%);
  pointer-events: none; /* Elimina la capacidad de hacer clic en el icono */
}

table {
  width: 100%;
  border-collapse: collapse;
  background-color: white;
}

thead {
  background-color: #231F57;
  color: #30BDBC;
  border-bottom: 0.3rem solid #30BDBC;
}

th {
  padding: 1.2rem 1.5rem;
  font-size: 1.7rem;
}

td {
  padding: 1rem 1.5rem;
  background-color: #EAECF0;
}

th, td {
  text-align: left;
